body {
  background: linear-gradient(135deg, #ece9e6, #fff);
  height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
}

main {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 250px);
  display: flex;
}

.container {
  text-align: center;
  background: #ffffffe6;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

h1 {
  color: #333;
  text-shadow: 1px 1px 2px #0000001a;
  margin-bottom: 20px;
  font-size: 2em;
}

#countdown {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.countdown-wrapper {
  flex-direction: row;
  gap: 15px;
  font-size: 1.2em;
  display: flex;
}

.countdown-wrapper div {
  background: #fff;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  animation: 1.5s ease-in-out fadeIn;
  display: flex;
}

.countdown-wrapper div span {
  color: #2c3e50;
  text-shadow: 1px 1px 2px #0000001a;
  margin-bottom: 5px;
  font-size: 2em;
  font-weight: bold;
  display: block;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: scale(.9);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.quote {
  color: #555;
  text-align: left;
  background: #f9f9f9;
  border-left: 5px solid #2c3e50;
  margin: 20px 0;
  padding: 15px 30px;
  font-size: 1.2em;
  font-style: italic;
  display: block;
  position: relative;
  box-shadow: 0 4px 8px #0000001a;
}

.quote:before, .quote:after {
  content: "“";
  color: #2c3e50;
  font-size: 3em;
  position: absolute;
}

.quote:before {
  top: -10px;
  left: 10px;
}

.quote:after {
  content: "”";
  bottom: -10px;
  right: 10px;
}

